import React from "react"
import { graphql, useStaticQuery } from "gatsby"

const GithubProjects = () => {
    const myRepoData = useStaticQuery(graphql`
    query {
        github {
            website: repository(owner:"kiddio", name:"kiddio.github.io") {
                id
                nameWithOwner
                url
            }
        }
    }
  `)
  console.log("++++++++++++++++++++\n" + myRepoData.github.website)
    return (
        <section>
            <br/>
            <br/>
            <br/>
            <p>
                Build Time Data: Gatsby repo{` `}
                <p>{myRepoData.github.website.url}</p>
                <p>{myRepoData.github.website.name}</p>
                <br/>
                <br/>
                <br/>
                <br/>

            </p>
        </section>
    )
}
export default GithubProjects